
.table
.search-tab-body tr{
    cursor: pointer;
}
.search-tab-body tr:hover{
    background-color: lightgray;
}
.search-tab-body tr td{
    width: 18%;
}
@media(max-width: 992px){
    .search-tab-body tr td{
        min-width: unset;
    }
    
}
.drop-toast p{
    font-size: 0.8em;
}
.drop-toast p:hover{
    background-color: white;
}
.spinnerData{
    width: 100%;
}

#toast-id-user{
    width: 175px; 
    overflow: hidden;
    text-overflow: ellipsis; 
}
.toast-imple{
    display: none;
    margin-top: 70px;
    margin-left: 10px;
    border-radius: 5px;
    background-color: rgba(255,255,255,.85);
    border: 1px solid rgba(0,0,0,.1);
    overflow: hidden;
    font-size: .875rem;
    background-clip: padding-box;
    box-shadow: 0 0.25rem 0.75rem rgba(0,0,0,.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    max-width: 500px;
    border-radius: .25rem;
    transition: opacity 1s linear;
}
.toast-imple::after, .toast-imple::before{
    box-sizing: border-box;
}

.toasts-header{
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: .25rem .75rem;
    color: #6c757d;
    background-color: rgba(255,255,255,.85);
    background-clip: padding-box;
    border-bottom: 1px solid rgba(0,0,0,.05);
}
.toasts-body{
    padding: .75rem;
}